    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedLight.otf');\r\n    font-weight: 100;\r\n}\r\n\r\n@font-face {\r\n    font-family: 'Sprat Type Condensed';\r\n    src: url('../../assets/fonts/sprat-type/Sprat-CondensedRegular.otf');\r\n    font-weight: normal;\r\n}\r\n\r\n// font families\r\n$FONT_DISPLAY: 'Sprat Type Condensed';\r\n$FONT_SUBHEAD: 'Overpass Mono', monospace;\r\n$FONT_BODY: 'franklin-gothic-urw', sans-serif;;\r\n\r\n// font sizes\r\n$FONT_SIZE_NAV: 30px; // changed from 38px\r\n$FONT_SIZE_LINK: 2.25vh; //changed from 24px\r\n$FONT_SIZE_LINE: 114px; // changed from 144px\r\n$FONT_SIZE_DESIGNER: 36px;\r\n$FONT_SIZE_TITLE: 48px;\r\n$FONT_SIZE_BODY: 18px;\r\n$FONT_SIZE_LABEL: 30px;\r\n$FONT_SIZE_LINE_PAGE_NAME: 108px;\r\n\r\n// font adjustments and weights\r\n$FONT_SPRAT_STRETCH: scaleY(1.2);\r\n$FONT_SPRAT_KERN: -0.11em;\r\n$FONT_SPRAT_WEIGHT: 200;\r\n$FONT_TITLE_WEIGHT: 400;\r\n$FONT_LINK_WEIGHT: 400;\r\n$FONT_LABEL_WEIGHT: 200;","/**\r\n * Styles shared between mobile and desktop\r\n */\r\n\r\n.logo {\r\n  fill: white;\r\n}\r\n\r\nbody {\r\n  margin: 0;\r\n  background-color: $LANDING_PAGE_BACKGROUND_COLOR;\r\n  font-family: 'Sprat Type';\r\n}\r\n\r\na {\r\n  color: white;\r\n}","/**\r\n * Constants for stylesheet\r\n */\r\n\r\n// sizing\r\n$X_MARGIN: 10vw;\r\n$BOTTOM_MARGIN: 10vh;\r\n$TOP_MARGIN: 36px;\r\n$DESKTOP_WIDTH: 768px;\r\n\r\n$TOP_MARGIN_MOBILE: 15px;\r\n\r\n/**\r\n * NOTE: this is completely arbitrary and loosely based upon values at\r\n * https://www.kylejlarson.com/blog/ipad-screen-size-guide-web-design-tips/.\r\n * - Desktop readjusted to 1240 since the navbar starts looking a bit squished\r\n * - Tablet set to 720 to be below minimum iPad browser width\r\n */\r\n$TABLET_WIDTH: 672px;\r\n$LINE_ELEMENT_HEIGHT: 142px;\r\n$NAV_ELEMENT_HEIGHT: 126px;\r\n\r\n// letter spacing\r\n$HEADER_TRACKING: -0.11em;\r\n$TITLE_TRACKING: -5px;\r\n$SUBHEAD_TRACKING: -2px;\r\n\r\n// Word spacing\r\n$HEADER_WORD_SPACING: 0.1em;\r\n\r\n// Background color\r\n$LANDING_PAGE_BACKGROUND_COLOR: #000000;\r\n$BACKGROUND_COLOR: #000000;\r\n\r\n// Keep track of z-indices\r\n$Z_INDEX_HEADER: 10;\r\n$Z_INDEX_MAIN_UI: 3;\r\n$Z_INDEX_BACKGROUND: -1;\r\n\r\n// Animation durations\r\n$SHORT_ANIMATION_DURATION: 200ms;\r\n$MEDIUM_ANIMATION_DURATION: 300ms;\r\n$LONG_ANIMATION_DURATION: 1000ms;","/**\r\n * Compiles all the styles together\r\n */\r\n\r\n// Shared all screens\r\n@import './constants.scss';\r\n@import './fonts.scss';\r\n@import './common.scss';\r\n\r\n// Desktop styles\r\n@media (min-width: $DESKTOP_WIDTH) {\r\n  @import './desktop/animations/common_animations.scss';\r\n  @import './desktop/desktop_constants.scss';\r\n  @import './desktop/index.scss';\r\n  @import './desktop/components/dots_lines.scss';\r\n  @import './desktop/components/landing_page.scss';\r\n  @import './desktop/components/about_page.scss';\r\n  @import './desktop/components/watch_page.scss';\r\n  @import './desktop/components/navbar.scss';\r\n  @import './desktop/components/navbar_line_page.scss';\r\n  @import './desktop/components/desktop_side_nav.scss';\r\n  @import './desktop/components/landing_page_prompt.scss';\r\n  @import './desktop/pages/line_page.scss';\r\n  @import './desktop/components/people_page.scss';\r\n  @import './desktop/animations/navbar_animations.scss';\r\n  @import './desktop/animations/vertical_line_animations.scss';\r\n  @import './desktop/animations/prompt_animations.scss';\r\n}\r\n\r\n// Mobile styles\r\n@media (max-width: $DESKTOP_WIDTH - 1) {\r\n  @import './mobile/mobile_constants.scss';\r\n  @import './mobile/index.scss';\r\n  @import './mobile/components/landing_page.scss';\r\n  @import './mobile/pages/line_page.scss';\r\n  @import './mobile/components/about_page.scss';\r\n  @import './mobile/components/dots_lines.scss';\r\n  @import './mobile/components/mobile_open_menu.scss';\r\n  @import './mobile/components/mobile_menu_line_list.scss';\r\n  @import './mobile/components/mobile_menu_nav_list.scss';\r\n}","/**\r\n * Common animations shared among many elements.\r\n */\r\n\r\n$ANIMATION_DURATION_BLUR: 500ms;\r\n@keyframes blur-in {\r\n  0% {\r\n    opacity: 0;\r\n    filter: blur(5px);\r\n  }\r\n  10% {\r\n    opacity: 0.75;\r\n  }\r\n  100% {\r\n    opacity: 1;\r\n    filter: blur(0);\r\n  }\r\n}","/**\r\n * Styles for the main site for DESKTOP\r\n */\r\n\r\n// lines and accents\r\n$CIRCLE_RAD : 12px;\r\n$OUTER : $X_MARGIN/8; // outer margin for vertical lines\r\n$LOWER : $BOTTOM_MARGIN/4;\r\n$UPPER : $BOTTOM_MARGIN*2.2;\r\n\r\nbody {\r\n  color: white;\r\n  height: 100vh;\r\n\twidth: 100%;\r\n\t-ms-overflow-style: none;  /* IE and Edge */\r\n\tscrollbar-width: none;  /* Firefox */\r\n\r\n\t&::-webkit-scrollbar {\r\n\t\tdisplay: none;\r\n\t}\r\n}\r\n\r\n.mobile {\r\n  display: none;\r\n}\r\n\r\n.main-page {\r\n\tdisplay: flex;\r\n\tflex-direction: column;\r\n\talign-items: center;\r\n\tjustify-content: center;\r\n}\r\n\r\n/* Display font */\r\nh1{\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: 100;\r\n\tfont-size: 4em;\r\n\tletter-spacing: -.11em;\r\n\tword-spacing: .1em;\r\n\tmargin-bottom: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 1 */\r\nh2 {\r\n  font-family: 'Sprat Type Condensed';\r\n  font-weight: normal;\r\n\tfont-size: 1.5em;\r\n\tletter-spacing: -.01em;\r\n\tmargin-top: 0;\r\n\tmargin-bottom: 1em;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Subheader 2 */\r\nh3 {\r\n\tfont-family: 'Overpass Mono', monospace;\r\n\tfont-weight: 400;\r\n\tfont-size: 1em;\r\n\tletter-spacing: -0.05em;\r\n\tmargin-bottom: 0;\r\n\tmargin-top: 0;\r\n\ttext-transform: uppercase;\r\n}\r\n\r\n/* Body text */\r\np {\r\n\tfont-family: 'franklin-gothic-urw', sans-serif;\r\n\tfont-weight: 400;\r\n\tfont-style: normal;\r\n\tfont-size: 1em;\r\n\tmargin-top: 0.5em;\r\n}\r\n\r\n// Common elements\r\n.title-theme {\r\n\tposition: absolute;\r\n\tz-index: $Z_INDEX_HEADER;\r\n\ttop: $TOP_MARGIN;\r\n\tfont-size: $FONT_SIZE_TITLE;\r\n\ttransform: $FONT_SPRAT_STRETCH;\r\n\tfont-weight: $FONT_TITLE_WEIGHT;\r\n\tletter-spacing: $TITLE_TRACKING;\r\n\topacity: 0;\r\n\ttransition-duration: 1s;\r\n\tcursor: pointer;\r\n\r\n\t// Title is not visible when no line is selected\r\n\t&.default {\r\n\t\tanimation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n\t}\r\n\t&.visible {\r\n\t\topacity: 1;\r\n\t}\r\n}","/**\r\n * Style definitions for overlayed lines and dots.\r\n */\r\n\r\n// TODO: this dot is poorly defined because it has too many specific variables,\r\n// making it hard to extend and use in general. See .dot-basic.\r\n.dot {\r\n  width: $CIRCLE_RAD;\r\n  height: $CIRCLE_RAD;\r\n  background-color: white;\r\n  border-radius: 100%;\r\n  position: absolute;\r\n  margin-bottom: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.dot-basic {\r\n  width: $CIRCLE_RAD;\r\n  height: $CIRCLE_RAD;\r\n  background-color: white;\r\n  border-radius: 100%;\r\n}\r\n\r\n.line {\r\n  height: 1px;\r\n  background-color: white;\r\n}\r\n\r\n.vertical-line {\r\n  height: 100vh;\r\n  top: 0;\r\n  position: absolute;\r\n  border-left: 1px solid white;\r\n  border-right: 1px solid white;\r\n  pointer-events: none;\r\n  opacity: 0;\r\n}\r\n\r\n#outer-lines {\r\n  left: $OUTER;\r\n  right: $OUTER;\r\n}\r\n\r\n#inner-lines {\r\n  left: $X_MARGIN/4;\r\n  right: $X_MARGIN/4;\r\n}\r\n\r\n.lower {\r\n  bottom: 0;\r\n}\r\n\r\n.upper {\r\n  top: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.left-dot {\r\n  left: $OUTER;\r\n  margin-left: -$CIRCLE_RAD/2;\r\n}\r\n\r\n.right-dot {\r\n  right: $OUTER;\r\n  margin-right: -$CIRCLE_RAD/2;\r\n}","/**\r\n * First page the user sees, the landing page.\r\n *\r\n * Contains a 3D asset in the back.\r\n */\r\n\r\n$DESIGNERS_NAME_HEIGHT: 46px;\r\n$SEE_MORE_WRAPPER_MARGIN: 1.25vw;\r\n$ARROW_WIDTH: 14px;\r\n$SEE_MORE_BOTTOM_MARGIN: 12px;\r\n\r\n.landing-page-background {\r\n  background-color: black;\r\n  width: 100%;\r\n  height: 100vh;\r\n  opacity: 0;\r\n  transition-duration: $LONG_ANIMATION_DURATION;\r\n\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n  }\r\n\r\n  &.visible {\r\n    opacity: 1;\r\n  }\r\n\r\n  #landing-page-cube {\r\n    width: 100%;\r\n    height: 100vh;\r\n  }\r\n}\r\n\r\n#landing-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  justify-content: center;\r\n  overflow: hidden;\r\n\r\n  &.desktop-about-page-open {\r\n    .landing-page-background {\r\n      opacity: 0.65;\r\n      transition-duration: 1s;\r\n    }\r\n\r\n    #main-screen #curr-line {\r\n      opacity: 0;\r\n    }\r\n  }\r\n  &.desktop-watch-page-open {\r\n    .landing-page-background {\r\n      opacity: 0.65;\r\n      transition-duration: 1s;\r\n    }\r\n\r\n    #main-screen #curr-line {\r\n      opacity: 0;\r\n    }\r\n  }\r\n\r\n  .faded {\r\n    opacity: 0;\r\n    transition: 0.2s;\r\n  }\r\n\r\n  .notFaded {\r\n    opacity: 1;\r\n    transition: 0.2s;\r\n  }\r\n\r\n  #main-screen {\r\n    flex-direction: row;\r\n\r\n    &.desktop-landing-page-load {\r\n      opacity: 0;\r\n    }\r\n\r\n    #curr-line {\r\n      position: absolute;\r\n      left: $X_MARGIN/2;\r\n      width: 100vw - $X_MARGIN/2;\r\n      bottom: 40vh;\r\n      display: block;\r\n\r\n      #line-name {\r\n        font-size: $FONT_SIZE_LINE;\r\n        text-transform: uppercase;\r\n        // transform: $FONT_SPRAT_STRETCH; // Shouldn't be any stretch\r\n        font-weight: $FONT_SPRAT_WEIGHT;\r\n        letter-spacing: $HEADER_TRACKING;\r\n        word-spacing: $HEADER_WORD_SPACING;\r\n      }\r\n\r\n      #below-line-name {\r\n        display: flex;\r\n        flex-direction: row;\r\n\r\n        #designers-name {\r\n          display: flex;\r\n          height: $DESIGNERS_NAME_HEIGHT;\r\n          font-size: $FONT_SIZE_DESIGNER;\r\n          font-family: $FONT_SUBHEAD;\r\n          text-transform: uppercase;\r\n          letter-spacing: $SUBHEAD_TRACKING;\r\n        }\r\n\r\n        #see-more-wrapper {\r\n          display: flex;\r\n          flex-grow: 1;\r\n          flex-direction: column;\r\n          align-items: flex-end;\r\n          // TODO: So I think the bug here is that on windows, the font has\r\n          // extra padding on the bottom, and on mac, the padding appears above\r\n          // the font. This means the padding has to be in different spots\r\n          // depending on platform... I think less padding is more correct, but\r\n          // idk why it doesn't work.\r\n          // padding-bottom: $SEE_MORE_BOTTOM_MARGIN;\r\n          margin-left: 24px;\r\n          right: $SEE_MORE_WRAPPER_MARGIN;\r\n\r\n          opacity: 0;\r\n          transition-duration: 1s;\r\n          transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n          &.show {\r\n            opacity: 1;\r\n          }\r\n\r\n          #more-info {\r\n            position: absolute;\r\n            display: flex;\r\n            flex-direction: row;\r\n            margin-right: $X_MARGIN/2 - $SEE_MORE_WRAPPER_MARGIN;\r\n            bottom: $FONT_SIZE_DESIGNER;\r\n            text-decoration: none;\r\n            cursor: pointer;\r\n\r\n            #more-info-text {\r\n              font-family: $FONT_BODY;\r\n              text-transform: uppercase;\r\n              font-size: $FONT_SIZE_LABEL;\r\n              font-weight: $FONT_LABEL_WEIGHT;\r\n              border-bottom: 2px solid white;\r\n              margin-right: 8px;\r\n            }\r\n\r\n            #arrow {\r\n              width: $ARROW_WIDTH;\r\n              height: $ARROW_WIDTH;\r\n              margin-top: $ARROW_WIDTH - 3px;\r\n              border-right: 2px solid white;\r\n              border-bottom: 2px solid white;\r\n              transform: rotate(-45deg);\r\n            }\r\n          }\r\n\r\n          #see-more-line-wrapper {\r\n            display: flex;\r\n            flex-direction: row;\r\n            align-items: center;\r\n            flex-grow: 1;\r\n            margin-right: $SEE_MORE_WRAPPER_MARGIN;\r\n            // Give a little space between the designer name and the circle\r\n            width: 99%;\r\n            // The font has some bottom spacing built-in, this centers the line\r\n            padding-top: 5px;\r\n            // By default, the line is not visible\r\n            transform: translateX(100%);\r\n            transition-duration: 500ms;\r\n            transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n            &.visible {\r\n              transform: translateX(0);\r\n            }\r\n\r\n            #see-more-line {\r\n              display: flex;\r\n              right: $OUTER;\r\n              border-bottom: 1px solid white;\r\n              flex-grow: 1;\r\n              height: 1px;\r\n              top: 1px;\r\n            }\r\n\r\n            // A bit different from other dots, so not going to use their styles\r\n            #see-more-dot {\r\n              bottom: $SEE_MORE_BOTTOM_MARGIN;\r\n              width: $CIRCLE_RAD;\r\n              height: $CIRCLE_RAD;\r\n              background-color: white;\r\n              border-radius: 100%;\r\n            }\r\n          }\r\n        }\r\n      }\r\n    }\r\n  }\r\n\r\n  .logo {\r\n    position: absolute;\r\n    z-index: $Z_INDEX_HEADER;\r\n\r\n    // Gonna be totally honest I don't think this is _quite_ accurate to get 21px under the logo\r\n    top: $TOP_MARGIN;\r\n    right: $X_MARGIN/2;\r\n    width: 5.5vh;\r\n    height: 7.3vh;\r\n    cursor: pointer;\r\n    transition-duration: 1s;\r\n\r\n    &.default {\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n    }\r\n    &.desktop-landing-page-load {\r\n      opacity: 0;\r\n    }\r\n    &.desktop-landing-page-cube-intro {\r\n      opacity: 0;\r\n    }\r\n    &.desktop-about-page-open {\r\n      opacity: 0;\r\n    }\r\n    &.desktop-watch-page-open {\r\n      opacity: 0;\r\n    }\r\n  }\r\n}\r\n","/**\r\n * About page for desktop\r\n */\r\n\r\n$BOTTOM_DESCRIPTOR_LINE_WIDTH: 38.3vw;\r\n\r\n#about-page-desktop {\r\n  position: absolute;\r\n  display: flex;\r\n  flex-direction: column;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  opacity: 0;\r\n\r\n  &.desktop-about-page-open {\r\n    opacity: 1;\r\n  }\r\n\r\n  #headline {\r\n    display: flex;\r\n    flex-direction: row;\r\n    margin-top: 20vh;\r\n\r\n    #headline-text {\r\n      margin-left: $X_MARGIN/2;\r\n      font-family: 'Sprat Type Condensed';\r\n      font-weight: 100;\r\n      letter-spacing: -0.12em;\r\n      font-size: 7vw;\r\n      width: 75vw;\r\n      word-spacing: 0.1em;\r\n    }\r\n\r\n    #right-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n\r\n      .dot-basic {\r\n        display: flex;\r\n      }\r\n\r\n      .line {\r\n        display: flex;\r\n        flex-grow: 1;\r\n\r\n      }\r\n    }\r\n  }\r\n\r\n  #bottom-descriptor {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    position: absolute;\r\n    bottom: 7vh;\r\n    width: 100vw;\r\n\r\n    #left-bar {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      justify-content: center;\r\n      min-width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\r\n\r\n      .dot-basic {\r\n        display: flex;\r\n      }\r\n\r\n      .line {\r\n        position: absolute;\r\n        width: $BOTTOM_DESCRIPTOR_LINE_WIDTH;\r\n      }\r\n    }\r\n\r\n    #description {\r\n      border: 1px solid white;\r\n      padding-top: 48px;\r\n      padding-bottom: 48px;\r\n      padding-left: 65px;\r\n      padding-right: 45px;\r\n      margin-right: $X_MARGIN*3/8;\r\n\r\n      font-family: $FONT_BODY;\r\n      font-size: 18px;\r\n    }\r\n  }\r\n}","/**\r\n * Watch page for desktop. Similar to the about page where it is not a\r\n * standalone page, and lives inside the landing page. Its visibility is\r\n * controlled by the landing page state.\r\n */\r\n\r\n$MARGIN_HORIZONTAL_COUNTDOWN: 10vw;\r\n\r\n#watch-page-desktop {\r\n  position: absolute;\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n  height: 100vh;\r\n  opacity: 0;\r\n\r\n  &.desktop-watch-page-open {\r\n    opacity: 1;\r\n  }\r\n\r\n  #countdown {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    width: 100vw - 2*$MARGIN_HORIZONTAL_COUNTDOWN;\r\n    margin-right: $MARGIN_HORIZONTAL_COUNTDOWN;\r\n    margin-left: $MARGIN_HORIZONTAL_COUNTDOWN;\r\n\r\n    .countdown-component {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      width: 25%;\r\n\r\n      .number {\r\n        font-size: 14.4vw;\r\n        font-weight: 300;\r\n        letter-spacing: -0.12em;\r\n      }\r\n\r\n      .label {\r\n        font-family: $FONT_SUBHEAD;\r\n        font-size: 2.36vw;\r\n        font-weight: 400;\r\n        letter-spacing: -0.05em;\r\n      }\r\n    }\r\n  }\r\n\r\n  #show-date {\r\n    position: absolute;\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: center;\r\n    align-items: center;\r\n    bottom: 8vw;\r\n    width: 100vw;\r\n\r\n    #show-date-text {\r\n      font-size: 4.44vw;\r\n      font-weight: 300;\r\n      margin-left: $X_MARGIN*3/8;\r\n      margin-right: 12px;\r\n    }\r\n\r\n    .right-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n\r\n      .line {\r\n        flex-grow: 1;\r\n        background-color: white;\r\n        height: 1px;\r\n      }\r\n    }\r\n  }\r\n\r\n  #show-time {\r\n    position: absolute;\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    bottom: 3vw;\r\n    width: 100vw;\r\n\r\n    #show-time-text {\r\n      font-size: 4.44vw;\r\n      font-weight: 300;\r\n      letter-spacing: -0.12em;\r\n      margin-right: $X_MARGIN*3/8;\r\n      margin-left: 12px;\r\n    }\r\n\r\n    .left-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      flex-grow: 1;\r\n      margin-left: 12px;\r\n\r\n      .line {\r\n        flex-grow: 1;\r\n        background-color: white;\r\n        height: 1px;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Landing page navbar\r\n */\r\n\r\n$UNDERLINE_CIRCLE_WIDTH: 8px;\r\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\r\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\r\n\r\n@keyframes underline-animation {\r\n  from {\r\n    width: 0px;\r\n  } to {\r\n    width: $UNDERLINE_WIDTH;\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-left-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-right-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n.navbar {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n  // Put this navbar at the bottom\r\n  position: fixed;\r\n  bottom: $OUTER;\r\n\r\n  border-top: 1px solid white;\r\n  border-bottom: 1px solid white;\r\n  padding-top: 2.5vh;\r\n  padding-bottom: 2.5vh;\r\n  transition-duration: 1s;\r\n\r\n  &.default {\r\n    .navbar-container {\r\n      animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n    }\r\n  }\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n\r\n    // We need to keep this because the navbar will have opacity: 1 at the end\r\n    // of its animation\r\n    .navbar-container {\r\n      opacity: 0;\r\n      pointer-events: none;;\r\n    }\r\n  }\r\n  &.desktop-landing-page-cube-intro {\r\n    .navbar-container {\r\n      opacity: 0;\r\n      pointer-events: none;;\r\n    }\r\n  }\r\n  &.desktop-about-page-open {\r\n    opacity: 0;\r\n  }\r\n  &.desktop-watch-page-open {\r\n    opacity: 0;\r\n  }\r\n\r\n  .navbar-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    width: 100vw - 2*3*$OUTER;\r\n    transition-duration: 1s;\r\n  }\r\n\r\n  .navBall {\r\n    margin: auto;\r\n    display: block;\r\n    transform: scale(.9, .8);\r\n    opacity: 0;\r\n    transition: .5s;\r\n  }\r\n\r\n  .underline-circle {\r\n    position: absolute;\r\n    width: $UNDERLINE_CIRCLE_WIDTH;\r\n    height: $UNDERLINE_CIRCLE_WIDTH;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n  }\r\n\r\n  .underline {\r\n    position: absolute;\r\n    width: 0px;\r\n    height: 1px;\r\n    background-color: white;\r\n  }\r\n\r\n  .navbar-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    transition: .5s;\r\n    border-bottom: 2px solid;\r\n    border-color: rgba(255, 255, 255, 0);\r\n    padding-bottom: $BOTTOM_MARGIN*0.05;\r\n    transition: .5s;\r\n\r\n    .underline-animation {\r\n      margin-top: 4px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      width: 100%;\r\n      height: 12px;\r\n      opacity: 0;\r\n      transition: $MEDIUM_ANIMATION_DURATION;\r\n    }\r\n\r\n    &.selected {\r\n      .navBall {\r\n        opacity: 1;\r\n      }\r\n\r\n      .underline-animation {\r\n        opacity: 1;\r\n\r\n        .left {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\r\n        }\r\n\r\n        .right {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\r\n        }\r\n\r\n        .underline {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\r\n        }\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Line page navbar\r\n */\r\n\r\n$UNDERLINE_CIRCLE_WIDTH: 8px;\r\n$UNDERLINE_WIDTH: 32px + 2*$UNDERLINE_CIRCLE_WIDTH;\r\n$UNDERLINE_CIRCLE_TRANSLATE_X: $UNDERLINE_WIDTH/2;\r\n$TOOLTIP_CIRCLE_WIDTH: 10px;\r\n$TOOLTIP_CIRCLE_HEIGHT: 8px;\r\n$TOOLTIP_MARGIN: 10px;\r\n$TOOLTIP_TICK_LENGTH: 27px;\r\n\r\n@keyframes underline-animation {\r\n  from {\r\n    width: 0px;\r\n  } to {\r\n    width: $UNDERLINE_WIDTH;\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-left-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX(-$UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n@keyframes underline-circle-right-animation {\r\n  from {\r\n    transform: translateX(0);\r\n  } to {\r\n    transform: translateX($UNDERLINE_CIRCLE_TRANSLATE_X);\r\n  }\r\n}\r\n\r\n.navbar-line-page {\r\n  display: flex;\r\n  flex-direction: column;\r\n  justify-content: center;\r\n  align-items: center;\r\n  width: 100vw;\r\n  margin-top: 6.66vw;\r\n\r\n  border-top: 1px solid white;\r\n  border-bottom: 1px solid white;\r\n  padding-top: 2.5vh;\r\n  padding-bottom: 2.5vh;\r\n\r\n  .navbar-container {\r\n    display: flex;\r\n    flex-direction: row;\r\n    justify-content: space-between;\r\n    align-items: center;\r\n    width: 100vw - 2*3*$OUTER;\r\n  }\r\n\r\n  .navBall {\r\n    margin: auto;\r\n    display: block;\r\n    transform: scale(.9, .8);\r\n    opacity: 0;\r\n    transition: .5s;\r\n  }\r\n\r\n  .underline-circle {\r\n    position: absolute;\r\n    width: $UNDERLINE_CIRCLE_WIDTH;\r\n    height: $UNDERLINE_CIRCLE_WIDTH;\r\n    background-color: white;\r\n    border-radius: 100%;\r\n  }\r\n\r\n  .underline {\r\n    position: absolute;\r\n    width: 0px;\r\n    height: 1px;\r\n    background-color: white;\r\n  }\r\n\r\n  .navbar-item {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: $FONT_SIZE_NAV;\r\n    cursor: pointer;\r\n    transform: $FONT_SPRAT_STRETCH;\r\n    font-weight: $FONT_SPRAT_WEIGHT;\r\n    transition: .5s;\r\n    border-bottom: 2px solid;\r\n    border-color: rgba(255, 255, 255, 0);\r\n    padding-bottom: $BOTTOM_MARGIN*0.05;\r\n    transition: .5s;\r\n    text-decoration: none;\r\n\r\n    .underline-animation {\r\n      margin-top: 4px;\r\n      display: flex;\r\n      align-items: center;\r\n      justify-content: center;\r\n      width: 100%;\r\n      height: 12px;\r\n      opacity: 0;\r\n      transition: $MEDIUM_ANIMATION_DURATION;\r\n    }\r\n\r\n    &.selected {\r\n      .navBall {\r\n        opacity: 1;\r\n      }\r\n\r\n      .underline-animation {\r\n        opacity: 1;\r\n\r\n        .left {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-left-animation forwards;\r\n        }\r\n\r\n        .right {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-circle-right-animation forwards;\r\n        }\r\n\r\n        .underline {\r\n          animation: $MEDIUM_ANIMATION_DURATION ease-in-out underline-animation forwards;\r\n        }\r\n      }\r\n    }\r\n\r\n    &:hover .tooltip {\r\n      opacity: 1;\r\n    }\r\n\r\n    // Special margin rule for the first line since it doesn't fit\r\n    &#line-0 .tooltip .wrapper {\r\n      margin-left: 82px;\r\n\r\n      #left-top {\r\n        margin-left: 82px;\r\n      }\r\n    }\r\n\r\n    .tooltip {\r\n      display: flex;\r\n      flex-direction: column;\r\n      align-items: center;\r\n      position: absolute;\r\n      bottom: 52px;\r\n      opacity: 0;\r\n      transition-duration: 200ms;\r\n\r\n      .vertical-tick {\r\n        width: 1px;\r\n        height: $TOOLTIP_TICK_LENGTH;\r\n        border-right: 1px solid white;\r\n      }\r\n\r\n      .wrapper {\r\n        display: flex;\r\n        flex-direction: row;\r\n        justify-content: center;\r\n        border: 1px solid white;\r\n        padding-left: 10px;\r\n        padding-right: 10px;\r\n        background-color: $LANDING_PAGE_BACKGROUND_COLOR;\r\n\r\n        .label {\r\n          border-left: 1px solid white;\r\n          border-right: 1px solid white;\r\n          font-family: $FONT_SUBHEAD;\r\n          font-weight: 400;\r\n          font-size: 18px;\r\n          text-transform: uppercase;\r\n          white-space: nowrap;\r\n\r\n          padding-top: 8px;\r\n          padding-bottom: 6px;\r\n          padding-left: $TOOLTIP_MARGIN;\r\n          padding-right: $TOOLTIP_MARGIN;\r\n        }\r\n\r\n        .dot-basic {\r\n          width: $TOOLTIP_CIRCLE_WIDTH;\r\n          height: $TOOLTIP_CIRCLE_HEIGHT;\r\n          position: absolute;\r\n        }\r\n\r\n        #left-top {\r\n          top: -$TOOLTIP_CIRCLE_WIDTH/2 + 1;\r\n          left: $TOOLTIP_CIRCLE_WIDTH/2 + 2;\r\n        }\r\n\r\n        #right-bottom {\r\n          right: $TOOLTIP_CIRCLE_WIDTH/2 + 1;\r\n          bottom: $TOOLTIP_TICK_LENGTH - $TOOLTIP_CIRCLE_WIDTH/2 + 1;\r\n        }\r\n      }\r\n\r\n      &.inactive {\r\n        opacity: 0;\r\n      }\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Navigation sidebar on desktop\r\n */\r\n\r\n.links {\r\n  position: absolute;\r\n  top: $TOP_MARGIN;\r\n  left: 0;\r\n  transition-duration: 1s;\r\n\r\n  &.default {\r\n    animation: blur-in $ANIMATION_DURATION_BLUR forwards;\r\n  }\r\n  &.desktop-landing-page-load {\r\n    opacity: 0;\r\n  }\r\n  &.desktop-landing-page-cube-intro {\r\n    opacity: 0;\r\n  }\r\n\r\n  .link-wrapper {\r\n    display: flex;\r\n    flex-direction: row;\r\n    margin-bottom: 4px; //changed from 8px\r\n    transition-duration: 1s;\r\n\r\n    &.selected .left-bar {\r\n      opacity: 1;\r\n    }\r\n\r\n    .left-bar {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: center;\r\n      opacity: 0;\r\n\r\n      .line {\r\n        display: flex;\r\n        flex-grow: 1;\r\n        padding-right: $X_MARGIN/4;\r\n      }\r\n\r\n      .dot-basic {\r\n        margin-left: -$CIRCLE_RAD/2;\r\n        margin-right: 14px;\r\n      }\r\n    }\r\n\r\n    .link {\r\n      font-family: $FONT_BODY;\r\n      font-size: $FONT_SIZE_LINK;\r\n      text-transform: uppercase;\r\n      font-weight: $FONT_LINK_WEIGHT;\r\n      display: block;\r\n      color: white;\r\n      text-decoration: none;\r\n    }\r\n  }\r\n}\r\n","/**\r\n * Prompt that appears after the initial load and before a user enters the main\r\n * site.\r\n */\r\n\r\n$MARGIN_BAR: 12px;\r\n$BAR_WIDTH: 74vw;\r\n\r\n#landing-page-prompt {\r\n  display: flex;\r\n  flex-direction: column;\r\n  align-items: center;\r\n  width: 100vw;\r\n  position: absolute;\r\n  opacity: 0;\r\n\r\n  &.hidden {\r\n    display: none;\r\n  }\r\n\r\n  .bar {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    width: 100vw;\r\n\r\n    .line {\r\n      display: flex;\r\n      flex-grow: 1;\r\n    }\r\n\r\n    .hidden {\r\n      width: 100vw - $BAR_WIDTH;\r\n    }\r\n  }\r\n\r\n  .right-bar {\r\n    margin-bottom: $MARGIN_BAR;\r\n  }\r\n\r\n  .left-bar {\r\n    margin-top: $MARGIN_BAR;\r\n  }\r\n\r\n  #title {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    font-size: 13vh;\r\n    font-weight: 300;\r\n    letter-spacing: -0.12em;\r\n  }\r\n\r\n  #enter-site {\r\n    margin-top: 12px;\r\n    font-family: $FONT_SUBHEAD;\r\n    font-size: 24px;\r\n    cursor: pointer;\r\n  }\r\n}","/**\r\n * Styles for the page showing each individual line\r\n */\r\n\r\n$LEFT_MARGIN: 25vw;\r\n$TITLE_MARGIN_TOP: 15vw;\r\n\r\n$SLIDE_AMOUNT_A: 6.66vw;\r\n$SLIDE_AMOUNT_B: -2.8vw;\r\n$SLIDE_AMOUNT_C: 6.4vw;\r\n$SLIDE_AMOUNT_D: 16.6vw;\r\n$BACK_BUTTON_SIZE: 24px;\r\n$BACK_BUTTON_STROKE_WIDTH: 3px;\r\n\r\n// Animations\r\n@keyframes slide-image-a {\r\n  from {\r\n    transform: translateY(0);\r\n  } to {\r\n    transform: translateY($SLIDE_AMOUNT_A);\r\n  }\r\n}\r\n\r\n@keyframes slide-image-b {\r\n  from {\r\n    transform: translateY(0);\r\n  } to {\r\n    transform: translateY($SLIDE_AMOUNT_B);\r\n  }\r\n}\r\n\r\n@keyframes slide-image-c {\r\n  from {\r\n    transform: translateY(0);\r\n  } to {\r\n    transform: translateY($SLIDE_AMOUNT_C);\r\n  }\r\n}\r\n\r\n@keyframes slide-image-d {\r\n  from {\r\n    transform: translateY(0);\r\n  } to {\r\n    transform: translateY($SLIDE_AMOUNT_D);\r\n  }\r\n}\r\n\r\n#line-page {\r\n  width: 100vw;\r\n  padding-bottom: 2.5vh;\r\n\r\n  #background {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: flex-end;\r\n    position: absolute;\r\n    top: 0;\r\n    z-index: -1;\r\n\r\n    // TODO: did we want to do a gradual opacity fade for the background?\r\n    #first {\r\n      background-size: cover;\r\n      height: 56.25vw;\r\n      width: 100vw;\r\n      opacity: 0.9;\r\n    }\r\n\r\n    #second {\r\n      background-size: cover;\r\n      height: 46vw;\r\n      width: 80vw;\r\n      margin-top: 14px;\r\n      opacity: 0.6;\r\n    }\r\n  }\r\n\r\n  #top-title {\r\n    position: fixed;\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: center;\r\n    width: 100vw;\r\n    margin-top: $TOP_MARGIN;\r\n    font-family: 'Sprat Type Condensed';\r\n    font-size: $FONT_SIZE_TITLE;\r\n    font-weight: 100;\r\n    letter-spacing: -0.05em;\r\n    text-decoration: none;\r\n  }\r\n\r\n  .main-content {\r\n    display: flex;\r\n    flex-direction: column;\r\n    align-items: flex-start;\r\n    margin-left: $LEFT_MARGIN;\r\n\r\n    #name {\r\n      // TODO: this is really bad style for font sizes and letter spacing. Not\r\n      // scalable\r\n      margin-top: $TITLE_MARGIN_TOP;\r\n      font-family: 'Sprat Type Condensed';\r\n      font-size: $FONT_SIZE_LINE_PAGE_NAME;\r\n      font-weight: 100;\r\n      letter-spacing: -0.12em;\r\n      text-transform: uppercase;\r\n      word-spacing: $HEADER_WORD_SPACING;\r\n    }\r\n\r\n    #designers {\r\n      display: flex;\r\n      flex-direction: row;\r\n      align-items: stretch;\r\n      margin-top: 10px;\r\n      width: 100%;\r\n\r\n      #designers-text {\r\n        display: flex;\r\n        font-family: $FONT_SUBHEAD;\r\n        font-weight: 400;\r\n        font-size: 24px;\r\n        text-transform: uppercase;\r\n      }\r\n\r\n      #right-bar {\r\n        display: 