px;\r\n      letter-spacing: -0.12em;\r\n    }\r\n  }\r\n\r\n  #mobile-open-menu {\r\n    width: $MOBILE_MENU_OPEN_SIZE;\r\n    height: $MOBILE_MENU_OPEN_SIZE;\r\n    display: flex;\r\n    justify-content: center;\r\n    align-items: center;\r\n    margin-left: $LEFT_MARGIN;\r\n    z-index: $Z_INDEX_HEADER+1;\r\n\r\n    #mobile-open-menu-sign {\r\n      font-size: 40px;\r\n      margin-bottom: 10px;\r\n    }\r\n\r\n    #mobile-open-menu-border {\r\n      position: absolute;\r\n      border: 1px solid white;\r\n      transform: rotate(45deg);\r\n      width: $MOBILE_MENU_OPEN_SIZE;\r\n      height: $MOBILE_MENU_OPEN_SIZE;\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the lines in the show\r\n */\r\n\r\n#mobile-menu-line-list {\r\n  position: absolute;\r\n  top: $LANDING_PAGE_HEADER_HEIGHT;\r\n  transition-duration: 1s;\r\n  transition-timing-function: cubic-bezier(0.075, 0.82, 0.165, 1);\r\n\r\n  // Initially, the line menu is hidden to the right\r\n  transform: translateX(100vw);\r\n\r\n  &.mobile-line-menu-open {\r\n    z-index: 3;\r\n    transform: translateX(0);\r\n  }\r\n\r\n  .line-element {\r\n    display: flex;\r\n    flex-direction: row;\r\n    align-items: center;\r\n    width: 100%;\r\n    border-bottom: 1px solid white;\r\n    background-color: black;\r\n    text-decoration: none;\r\n    cursor: pointer;\r\n\r\n    .line-element-body {\r\n      margin-top: 25px;\r\n      margin-bottom: 29px;\r\n      margin-left: 32px;\r\n\r\n      .title {\r\n        font-family: 'Sprat Type Condensed';\r\n        font-weight: 100;\r\n        font-size: 36px;\r\n        text-transform: uppercase;\r\n        transform: scale(1, 1.2);\r\n      }\r\n\r\n      .designers {\r\n        margin-top: 8px;\r\n        font-size: 20px;\r\n        font-family: $FONT_SUBHEAD;\r\n        text-transform: uppercase;\r\n        letter-spacing: -0.05em;\r\n      }\r\n    }\r\n  }\r\n}","/**\r\n * Styles for the menu that shows all the nav links\r\n */\r\n\r\n#mobile-m